@charset "UTF-8";
* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
}

/*--------------------*/
body {
  font-family: Montserrat, sans-serif;
}

.wrapper {
  overflow: hidden;
}

.container {
  margin: 0 auto;
  padding: 15px;
  max-width: 1240px;
}

body._lock {
  overflow: hidden;
}

.slider {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.slider__items {
  display: flex;
  width: 100%;
  position: relative;
}
.slider__item {
  position: absolute;
  left: 50%;
  top: 0;
  width: 100%;
  opacity: 0;
  transform: translate(100%) scale(0.7);
  transition: transform 1s ease, opacity 0.5s ease-in;
}
.slider__item_active {
  transform: translate(-50%) scale(1);
  opacity: 1;
}
.slider__indicators {
  display: flex;
  align-items: center;
  margin-top: 30px;
}
.slider__indicator + .slider__indicator {
  margin-left: 5px;
}
.slider__indicator {
  position: relative;
  width: 25px;
  height: 25px;
  cursor: pointer;
}
.slider__indicator::before {
  content: "";
  box-sizing: content-box;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  display: block;
  width: 14px;
  height: 14px;
  border: 1px solid #000;
  border-radius: 50%;
  transition: all 0.2s ease-in;
}
.slider__indicator_active::before, .slider__indicator:hover::before {
  width: 8px;
  height: 8px;
  border: 6px solid #000;
}
.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
  background-color: #fff;
}
.header__top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px 0px;
}
.header__brand {
  font-weight: 600;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
  text-transform: uppercase;
  color: navy;
  padding: 0px 15px;
}
@media (max-width: 850px) {
  .header__brand {
    font-size: 16px;
  }
}
.header__menu {
  padding: 30px 0px;
}
.header__container {
  position: relative;
  z-index: 15;
}
.header__main {
  position: relative;
  z-index: 8;
  display: flex;
  flex-direction: column;
  max-width: 1440px;
  width: 100%;
  margin: 0 auto;
}
@media (min-width: 767px) {
  .header__main {
    padding: 0 30px;
  }
}
@media (max-width: 767px) {
  .header__main {
    padding: 0 10px;
  }
}

.menu a {
  color: #6a6a6a;
}
.menu__body {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.menu__list {
  display: flex;
  align-items: center;
}
.menu__list a {
  font-weight: 600;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  transition: all 0.25s ease-in 0s;
}
@media (max-width: 850px) {
  .menu__list a {
    font-size: 12px;
  }
}
.menu__list a:hover {
  color: #000;
}
.menu__list > li {
  position: relative;
  padding: 10px 0px;
  padding-right: 15px;
}
.menu__list > li:last-child {
  padding-right: 0;
}
.menu__link_arrow {
  position: relative;
  padding: 0px 20px 0px 0px;
}
.menu__arrow {
  position: absolute;
  width: 18px;
  height: 12px;
  top: 30%;
  right: 5%;
  display: inline-block;
  transition: all 0.5s ease-in-out 0s;
}
.menu__sub-list {
  background-color: #fff;
}

body._touch .menu__list > li._active .menu__arrow {
  transform: rotate(-180deg);
}

body._touch .menu__list > li._active .menu__sub-list {
  opacity: 1;
  visibility: visible;
  transform: translate(0px, 0px);
  pointer-events: all;
}

body._pc .menu__list > li:hover .menu__arrow {
  transform: rotate(180deg);
}

.menu__burg {
  display: none;
}

.menu__brand-body {
  display: none;
}

.menu__mob-bottom {
  display: none;
}

@media (min-width: 767px) {
  .menu__sub-list {
    position: absolute;
    top: 100%;
    left: 0;
    min-width: 200px;
    transform: translate(0px, 10%);
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    transition: all 0.3s ease 0s;
    padding: 20px 20px;
    box-shadow: 4px 4px 35px rgba(0, 0, 0, 0.15), 0px 3px 35px rgba(0, 0, 0, 0.15);
    border-radius: 5px;
  }
  .menu__sub-list :not(:last-child) {
    padding: 0px 0px 20px 0px;
  }

  body._pc .menu__list > li:hover .menu__sub-list {
    opacity: 1;
    visibility: visible;
    transform: translate(0px, 0px);
    pointer-events: all;
  }
}
@media (max-width: 767px) {
  .menu__icon-burger {
    z-index: 5;
    display: block;
    position: relative;
    width: 27px;
    height: 28px;
    cursor: pointer;
    background: rgba(107, 158, 255, 0.85);
    border-radius: 50%;
  }

  .menu__icon-burger span,
.menu__icon-burger::before,
.menu__icon-burger::after {
    left: 50%;
    position: absolute;
    height: 2px;
    width: 75%;
    transition: all 0.3s ease 0s;
    background-color: #fff;
  }

  .menu__icon-burger::before,
.menu__icon-burger::after {
    content: "";
  }

  .menu__icon-burger::before {
    top: 30%;
    transform: translate(-50%, -50%);
  }

  .menu__icon-burger::after {
    bottom: 22%;
    transform: translate(-50%, -50%);
  }

  .menu__icon-burger span {
    top: 50%;
    transform: scale(1) translate(-50%, -50%);
  }

  .menu__icon-burger._active::before {
    top: -2%;
    left: 55%;
    transform: rotate(-45deg) translate(-90%, 100%);
  }

  .menu__icon-burger._active::after {
    bottom: 7%;
    left: 55%;
    transform: rotate(45deg) translate(-75%, 52%);
  }

  .menu__icon-burger._active span {
    opacity: 0;
  }

  .menu__icon-seach {
    display: inline-block;
  }

  .menu__brand-body {
    display: block;
    font-weight: 700;
    font-size: 14px;
    line-height: 140%;
    text-align: center;
    text-transform: uppercase;
    padding: 50px 0px 0px 0px;
  }

  .menu__burg-icons {
    display: flex;
    align-items: center;
    column-gap: 10px;
  }

  .menu__burg {
    display: flex;
    width: 100%;
    position: fixed;
    z-index: 5;
    justify-content: space-between;
    padding: 5px 10px;
  }

  .header__top {
    display: none;
  }

  .header__menu {
    padding: 0;
  }

  .header__icons {
    display: none;
  }

  .menu__mob-bottom {
    display: flex;
    flex-direction: column;
    text-align: center;
    row-gap: 12px;
    padding: 0px 0px 30px 0px;
  }
  .menu__mob-bottom a {
    color: #f5f5f5;
  }

  .menu__btn-mail {
    padding: 10px 10px 10px 36px;
    background: rgba(107, 158, 255, 0.85);
    border-radius: 9px;
    position: relative;
  }

  .menu__btn-mail::before {
    content: "";
    background: url("/img/icons/icon-mail27.svg") center/contain no-repeat;
    position: absolute;
    top: 50%;
    left: 10px;
    width: 20px;
    height: 14px;
    display: inline-block;
    transform: translateY(-50%);
  }

  .menu__btn-tel {
    background: rgba(107, 158, 255, 0.85);
    border-radius: 9px;
    padding: 10px 10px 10px 36px;
    position: relative;
    text-align: center;
  }

  .menu__btn-tel::before {
    content: "";
    background: url("/img/icons/icon-phone27.svg") center/contain no-repeat;
    position: absolute;
    top: 50%;
    left: 10px;
    width: 20px;
    height: 14px;
    display: inline-block;
    transform: translateY(-50%);
  }

  .menu__list {
    display: flex;
    flex-direction: column;
    align-content: center;
  }
  .menu__list > li {
    padding-right: 10px;
  }
  .menu__list a {
    font-weight: 700;
    font-size: 16px;
    color: #2e2e2e;
  }

  .menu__body {
    position: fixed;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100vh;
    background-color: #fff;
    padding: 50px 30px 0px 30px;
    overflow-y: auto;
    display: flex;
    flex-direction: column;
    align-content: center;
    justify-content: space-between;
    transition: all 0.3s ease-in-out 0s;
  }

  .menu__body._active {
    left: 0;
  }

  .header__container {
    padding: 0;
  }

  .menu__body::before {
    content: "";
    position: fixed;
    width: 100%;
    top: 0;
    left: 0%;
    height: 50px;
    background-color: #fff;
    z-index: 2;
    box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
  }

  body._pc .menu__list > li {
    display: flex;
    flex-direction: column;
  }

  .menu__arrow {
    top: 12px;
  }

  .menu__sub-list li {
    text-align: center;
  }
  .menu__sub-list li:not(:last-child) {
    padding: 0px 0px 25px 0px;
  }

  .menu__sub-list {
    opacity: 1;
    visibility: visible;
    position: relative;
    background-color: #fff;
    flex: 1 1 100%;
    margin: 20px 0px 0px 0px;
    display: none;
  }

  body._pc .menu__list > li:hover .menu__sub-list {
    display: block;
  }

  body._touch .menu__list > li._active .menu__sub-list {
    display: block;
  }
}
.footer {
  background-color: #F4F4F4;
}
.footer__container {
  display: flex;
  flex-direction: column;
}
.footer__body {
  padding: 47px 0px 54px 0px;
  display: flex;
}
@media (min-width: 600px) {
  .footer__body {
    justify-content: space-between;
    gap: 20px;
  }
}
@media (max-width: 600px) {
  .footer__body {
    flex-direction: column;
    row-gap: 20px;
    align-items: center;
  }
}
.footer__icons {
  display: flex;
  flex-direction: column;
  row-gap: 24px;
}
.footer__icon {
  display: flex;
  align-content: space-between;
  column-gap: 16px;
}
.footer__icon > span {
  display: inline-block;
  font-weight: 700;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  max-width: 350px;
}
.footer__contacts {
  line-height: 140%;
  color: #2e2e2e;
  max-width: 220px;
}
.footer__contacts > span {
  font-weight: 500;
  margin: 0px 0px 20px 0px;
  display: inline-block;
}
.footer__list :not(:last-child) {
  margin: 0px 0px 20px 0px;
}
.footer__copy {
  align-self: center;
}
.footer__link {
  position: relative;
  padding: 0px 0px 0px 20px;
}
.footer__link::before {
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
  font-size: 14px;
  position: absolute;
  top: 0;
  left: 0%;
}
.footer__link_adress::before {
  content: "\f3c5";
}
.footer__link_tel::before {
  content: "\f095";
}
.footer__link_mail::before {
  content: "\f0e0";
}

.back-to-top {
  position: fixed;
  bottom: 20px;
  right: 20px;
  z-index: 10;
  display: inline-block;
  padding: 10px;
  opacity: 0;
  color: #f5f5f5;
  background-color: #6b9eff;
  border-radius: 50%;
  transition: all 0.25s ease-in-out;
  cursor: pointer;
  pointer-events: none;
}
.back-to-top img {
  width: 20px;
}

.back-to-top_visible {
  opacity: 0.5;
  pointer-events: all;
}
.back-to-top_visible:hover {
  opacity: 1;
}

.heading-with-icon {
  display: flex;
  align-items: start;
}
.heading-with-icon__icon {
  width: 48px;
}
.heading-with-icon__text {
  margin-left: 10px;
  font-weight: 600;
  font-size: 16px;
  text-transform: uppercase;
  color: #000000;
}

.tiny-heading {
  font-weight: 600;
  font-size: 16px;
  color: #000000;
}

.big-heading {
  font-weight: 700;
  font-size: 32px;
}

.large-heading {
  font-weight: 700;
  font-size: 40px;
  text-transform: uppercase;
}
@media (max-width: 767px) {
  .large-heading {
    font-size: 36px;
  }
}
@media (max-width: 400px) {
  .large-heading {
    font-size: 28px;
  }
}

.text {
  font-weight: 400;
  line-height: 140%;
  color: #2e2e2e;
}
.text_size_m {
  font-size: 20px;
}
@media (max-width: 767px) {
  .text_size_m {
    font-size: 16px;
  }
}
@media (max-width: 400px) {
  .text_size_m {
    font-size: 14px;
  }
}
.text_size_s {
  font-size: 16px;
}
@media (max-width: 767px) {
  .text_size_s {
    font-size: 14px;
  }
}
@media (max-width: 400px) {
  .text_size_s {
    font-size: 12px;
  }
}

.button {
  display: inline-block;
  padding: 15px 50px;
  font-weight: 600;
  font-size: 20px;
  text-align: center;
  cursor: pointer;
}
.button_stroke {
  color: #3b518a;
  border: 1px solid #3b518a;
  border-radius: 10px;
  transition: color 0.25s ease-out, background-color 0.25s ease-out;
}
.button_stroke:hover {
  color: #f5f5f5;
  background-color: #3b518a;
}
.button_fill {
  color: #f5f5f5;
  background: #6b9eff;
  border: 2px solid rgba(255, 255, 255, 0.25);
  border-radius: 10px;
  transition: transform 0.25s ease-out;
}
.button_fill:hover {
  transform: translateY(-5px);
}
@media (max-width: 767px) {
  .button {
    font-size: 16px;
  }
}

.watch-button {
  position: relative;
  display: inline-flex;
  align-items: center;
  border: 1px solid #f5f5f5;
  border-radius: 40px;
  padding-left: 50px;
  height: 50px;
  overflow: hidden;
  transition: border-color 0.3s ease-in;
}
.watch-button__icon {
  position: absolute;
  right: 0;
  top: 50%;
  transform: translate(-1px, -50%);
  width: 50px;
  height: 50px;
}
.watch-button__secret {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  transform: translateX(-100%) translateX(50px);
  padding-right: 50px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 40px;
  background-color: #fff;
  transition: transform 0.3s ease-out;
}
.watch-button__icon {
  width: 50px;
  height: 50px;
}
.watch-button__text, .watch-button__secret-text {
  margin: 0 10px;
  font-weight: 600;
  font-size: 16px;
  text-transform: uppercase;
}
.watch-button__text {
  color: #f5f5f5;
}
.watch-button__secret-text {
  color: #2e2e2e;
}
.watch-button:hover {
  border-color: transparent;
}

.watch-button:hover .watch-button__secret {
  transform: translateX(0%);
}

.quote__text {
  position: relative;
  margin-bottom: 40px;
  padding: 0 20px;
  color: #2e2e2e;
}
.quote__text::before, .quote__text::after {
  position: absolute;
  font-weight: 900;
  font-size: 30px;
  color: #2e2e2e;
}
.quote__text::before {
  content: "“";
  transform: translateX(-150%);
}
.quote__text::after {
  content: "”";
  transform: translateX(50%);
}
.quote__author {
  font-style: italic;
  font-weight: 500;
  font-size: 20px;
  color: #2e2e2e;
}
@media (max-width: 400px) {
  .quote__text {
    font-size: 14px;
  }
}

.input, .textarea {
  background: #f5f5f5;
  border: 1px solid rgba(0, 0, 0, 0.05);
  border-radius: 5px;
  padding: 10px;
}
.input::placeholder, .textarea::placeholder {
  font-size: 16px;
  line-height: 20px;
  color: #5f5f5f;
}

.textarea {
  resize: vertical;
}

.contact-link {
  display: flex;
  align-items: center;
}
.contact-link__icon {
  width: 50px;
  height: 50px;
}
.contact-link__text {
  margin-left: 20px;
}

.intro {
  position: relative;
  padding: 60px 0;
}
@media (min-width: 1300px) {
  .intro {
    padding: 15vh 0;
  }
}
.intro__heading {
  max-width: 600px;
  margin: 0px 0px 20px 0px;
  color: #f5f5f5;
}
@media (max-width: 767px) {
  .intro__heading {
    font-size: 30px;
    padding: 10px 0px 26px 0px;
  }
}
@media (max-width: 320px) {
  .intro__heading {
    font-size: 20px;
    padding: 10px 0px 26px 0px;
  }
}
.intro__bg img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}

.appeal {
  position: relative;
  margin: 0px 0px 50px 0px;
}
.appeal__heading {
  color: #3b518a;
  margin-bottom: 100px;
}
@media (max-width: 900px) {
  .appeal__heading {
    margin: 0px 0px 30px 0px;
    text-align: center;
  }
}
.appeal__body {
  display: grid;
  column-gap: 20px;
}
@media (min-width: 900px) {
  .appeal__body {
    grid-template-columns: 3fr 1fr;
  }
}
@media (max-width: 900px) {
  .appeal__body {
    justify-items: center;
  }
}
.appeal__quote {
  max-width: 650px;
}
@media (max-width: 900px) {
  .appeal__quote {
    grid-row: 2/3;
  }
}
@media (max-width: 900px) {
  .appeal__photo {
    grid-row: 1/2;
    margin: 0px 0px 30px 0px;
  }
}
.appeal__photo img {
  width: 100%;
  max-width: 250px;
  object-fit: cover;
  border-radius: 10px;
}
.appeal__bg {
  position: absolute;
  top: 0;
  right: 0;
  z-index: -2;
  width: 45%;
  transform: translate(35%, -15%);
}

.latest-news-card {
  display: grid;
  max-width: 1240px;
  background-color: #fff;
  border-radius: 25px;
  overflow: hidden;
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.25);
}
.latest-news-card__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  filter: drop-shadow(5px 0px 19px rgba(0, 0, 0, 0.25));
}
.latest-news-card__content {
  display: flex;
  flex-direction: column;
  padding: 25px;
}
.latest-news-card__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.latest-news-card__heading {
  position: relative;
  max-width: 385px;
  padding-left: 20px;
}
.latest-news-card__heading::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  display: block;
  height: 100%;
  width: 10px;
  background-color: #3B518A;
}
.latest-news-card__date {
  margin-left: 20px;
  padding: 5px;
  font-weight: 500;
  font-size: 16px;
  color: #fff;
  background-color: #6B9EFF;
  border-radius: 5px;
}
.latest-news-card__text {
  margin-top: 25px;
  margin-bottom: 25px;
  max-width: 600px;
}
.latest-news-card__link {
  font-weight: 400;
  font-size: 16px;
  text-decoration: underline;
  color: #6B9EFF;
}
@media (min-width: 767px) {
  .latest-news-card {
    grid-template-columns: 2fr 3fr;
  }
}
@media (max-width: 1040px) and (min-width: 767px) {
  .latest-news-card {
    overflow: visible;
  }
  .latest-news-card__image {
    margin: 25px;
    width: calc(100% - 50px);
    height: auto;
    justify-self: center;
    border-radius: 20px;
  }
}
@media (max-width: 767px) {
  .latest-news-card {
    position: relative;
  }
  .latest-news-card__date {
    position: absolute;
    top: 25px;
    right: 25px;
  }
}

