.important-news-card {
    position: relative;
    display: flex;
    flex-direction: column;
    //align-items: flex-start;
    padding: 25px 30px 25px 60px;

    height: 100%;
    
    background-color: rgba(#F5F5F5, 0.8);
    box-shadow: 0px 16px 50px rgba(0, 0, 0, 0.15);
    border-radius: 11px;

    cursor: pointer;

    transition: all 0.3s ease-out;

    &__date {
        display: inline-block;
        padding: 5px;

        color: #f5f5f5;

        background-color: #6b9eff;
        border-radius: 5px;
    }

    &__text {
        margin-top: 25px;
        max-width: 400px;
    }
		
    &:hover{
        background-color: rgba(#F5F5F5, 1.0);
    }

    @media (max-width: 400px) {
        padding: 20px;
        border-radius: 0px;
    }

    @media (min-width: 550px) {
        
        &__date {
            position: absolute;
            left: -20px;
            top: 20px;
        }
    }

    @media (max-width: 550px) {
        padding: 20px;
        &__header {
            display: flex;
            align-items: center;
        }

        &__heading {
            order: -1;
        }

        &__date {
            margin-left: 5px;
        }
    }
}

.important-news-cards {
    &__items {
        display: grid;
		grid-template-columns: repeat(auto-fit, minmax(auto, 490px));
		
		gap: 30px;
		justify-content: space-around;
		
		@media (min-width: 900px) {
			grid-auto-rows: 1fr;
		}
    }
}

.important-news {
	background: url('/img/main/bg_wave.svg') center/cover no-repeat;

	padding: 30px 0;

    @media (max-width: 400px) {
        &__container {
            padding: 0px;
        }
        &__heading {
            text-align: center;
        }
    }
    
    &__heading {        
        color: rgba(#F5F5F5, 0.9);
	}

    &__cards {
        margin-top: 30px;
    }

	&__date {
		background: #6b9eff;
		border-radius: 5px;
		padding: 5px;
		color: #fff;
		font-weight: 500;
		font-size: 16px;
		line-height: 20px;

		position: absolute;
		top: 10%;
		left: -20px;
	}
}